import React from 'react';
import Profile from './Profile';
import About from './About';
import Background  from './Background';
const Body = (props)=>{
  return (
    <div class='body f'>
    {props.time>=100&&<Profile/>}
    {props.time>=250&&<About/>}
    {props.time>=450&&<Background/>}
    </div>
  );
}

export default Body;